import React, { useState } from 'react';
import { Layout, Menu } from 'antd';
import { UserOutlined, QuestionCircleOutlined } from '@ant-design/icons';
import SearchBar from './SearchBar';
import UserTable from './UserTable';
import SearchQuestion from './SearchQuestion';
import QuestionTable from './QuestionTable';

const { Header, Sider, Content } = Layout;

const App = () => {
  const [selectedKey, setSelectedKey] = useState('1');

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Header style={{
        display: 'flex',
        alignItems: 'center',
        backgroundColor: '#001529',
        padding: '0 24px'
      }}>
        <h1 style={{ fontSize: '24px', margin: 0, color: '#fff' }}>Quiz</h1>
      </Header>
      <Layout>
        <Sider width={155} theme="dark">
          <Menu
            mode="inline"
            theme="dark"
            defaultSelectedKeys={['1']}
            onClick={({ key }) => setSelectedKey(key)}
            style={{ height: '100%' }}
            items={[
              { key: '1', icon: <UserOutlined />, label: '' },
              { key: '2', icon: <QuestionCircleOutlined />, label: '' }
            ]}
          />
        </Sider>
        <Content style={{ padding: '24px', minHeight: 280, backgroundColor: '#f0f2f5' }}>
          {selectedKey === '1' && (
            <>
              <SearchBar />
              <UserTable />
            </>
          )}
          {selectedKey === '2' && (
            <>
              <SearchQuestion />
              <QuestionTable />
            </>
          )}
        </Content>
      </Layout>
    </Layout>
  );
};

export default App;
