import { Input, Button, Modal, message, Form } from 'antd';
import React, { useState, useRef } from 'react';
import AddNew from './AddNew';

const SearchBar = () => {
  const [open, setOpen] = useState(false);
  const formRef = useRef(null);

  const showModal = () => {
    setOpen(true);
  };

  const handleOk = () => {
    if (formRef.current) {
      formRef.current.submit();
    }
  };

  const handleCancel = () => {
    if (formRef.current) {
      formRef.current.reset();
    }
    setOpen(false);
  };

  const onSearch = (values) => {
    const keyword = values.keyword;
    if (!keyword || keyword.trim() === '') {
      message.warning('!');
      return;
    }
    console.log(':', keyword);
    message.info('');
  };

  const handleSubmit = (values) => {
    console.log(':', values);
    message.success('!');
    if (formRef.current) {
      formRef.current.reset();
    }
    setOpen(false);
  };

  return (
    <>
      <Form layout="inline" onFinish={onSearch} style={{ marginBottom: '16px' }}>
        <Form.Item label="" name="keyword">
          <Input placeholder="" style={{ width: 200 }} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
        <Form.Item>
          <Button type="primary" onClick={showModal}>
            
          </Button>
        </Form.Item>
      </Form>

      <Modal
        title=""
        open={open}
        onOk={handleOk}
        onCancel={handleCancel}
        okText=""
        cancelText=""
      >
        <AddNew ref={formRef} onSubmit={handleSubmit} />
      </Modal>
    </>
  );
};

export default SearchBar;
