import { Space, Table, Button, Modal, message, Form, Input } from 'antd';
import React, { useState } from 'react';

const formItemLayout = {
  labelCol: { xs: { span: 24 }, sm: { span: 6 } },
  wrapperCol: { xs: { span: 24 }, sm: { span: 18 } },
};

// 
const initialData = [
  {
    key: '1',
    id: 1,
    questionText: 'React',
    options: 'A. , B. , C. , D. ',
    answer: 'A',
  },
  {
    key: '2',
    id: 2,
    questionText: 'React',
    options: 'A. componentDidMount, B. componentDidUpdate, C. componentWillUnmount, D. ',
    answer: 'D',
  },
  {
    key: '3',
    id: 3,
    questionText: 'React',
    options: 'A. Redux, B. MobX, C. Context API, D. ',
    answer: 'D',
  },
  {
    key: '4',
    id: 4,
    questionText: 'React',
    options: 'A. , B. , C. , D. ',
    answer: 'D',
  },
  {
    key: '5',
    id: 5,
    questionText: 'React',
    options: 'A. React Router, B. Next.js, C. Reach Router, D. ',
    answer: 'D',
  },
];

const QuestionTable = () => {
  const [data, setData] = useState(initialData);
  const [editDialogVisible, setEditDialogVisible] = useState(false);
  const [currentQuestion, setCurrentQuestion] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize] = useState(5);
  const [form] = Form.useForm();

  const columns = [
    {
      title: '',
      dataIndex: 'id',
      key: 'id',
      width: 80,
    },
    {
      title: '',
      dataIndex: 'questionText',
      key: 'questionText',
      width: 250,
    },
    {
      title: '',
      dataIndex: 'options',
      key: 'options',
      width: 300,
    },
    {
      title: '',
      dataIndex: 'answer',
      key: 'answer',
      width: 80,
    },
    {
      title: '',
      key: 'action',
      render: (_, record) => (
        <Space size="small">
          <Button size="small" type="primary" onClick={() => handleEdit(record)}>
            
          </Button>
          <Button size="small" danger onClick={() => handleDelete(record)}>
            
          </Button>
        </Space>
      ),
    },
  ];

  const handleEdit = (record) => {
    setCurrentQuestion(record);
    // 
    const optionsArray = record.options.split(', ');
    const optionsObj = {};
    optionsArray.forEach((opt) => {
      const [letter, text] = opt.split('. ');
      optionsObj[letter.toLowerCase()] = text;
    });

    form.setFieldsValue({
      question: record.questionText,
      optiona: optionsObj.a || '',
      optionb: optionsObj.b || '',
      optionc: optionsObj.c || '',
      optiond: optionsObj.d || '',
      answer: record.answer.toLowerCase(),
    });
    setEditDialogVisible(true);
  };

  const handleEditSubmit = () => {
    form.validateFields()
      .then((values) => {
        console.log(':', values);
        message.success('!');
        setEditDialogVisible(false);
        form.resetFields();
      })
      .catch((error) => {
        console.log(':', error);
      });
  };

  const handleEditCancel = () => {
    setEditDialogVisible(false);
    form.resetFields();
  };

  const handleDelete = (record) => {
    Modal.confirm({
      title: '',
      content: ', ?',
      okText: '',
      cancelText: '',
      type: 'warning',
      onOk: () => {
        console.log(':', record.id);
        message.success('!');
        setData(data.filter(item => item.id !== record.id));
      },
      onCancel: () => {
        message.info('');
      },
    });
  };

  return (
    <>
      <Table
        columns={columns}
        dataSource={data}
        pagination={{
          current: currentPage,
          pageSize: pageSize,
          total: 100,
          showSizeChanger: false,
          position: ['bottomLeft'],
          onChange: (page) => {
            setCurrentPage(page);
            console.log('', page, '');
          },
        }}
      />

      <Modal
        title=""
        open={editDialogVisible}
        onOk={handleEditSubmit}
        onCancel={handleEditCancel}
        okText=""
        cancelText=""
        width={600}
      >
        <Form
          {...formItemLayout}
          form={form}
          name="editQuestion"
        >
          <Form.Item
            name="question"
            label=""
            rules={[{
              required: true,
              message: '!',
            }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="optiona"
            label="a"
            rules={[{
              required: true,
              message: 'a!',
            }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="optionb"
            label="b"
            rules={[{
              required: true,
              message: 'b!',
            }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="optionc"
            label="c"
            rules={[{
              required: true,
              message: 'c!',
            }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="optiond"
            label="d"
            rules={[{
              required: true,
              message: 'd!',
            }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="answer"
            label=""
            rules={[{
              required: true,
              message: '!',
            }]}
          >
            <Input placeholder="(a/b/c/d)" />
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
};

export default QuestionTable;
