import { Space, Table, Button, Modal, message, Form, Input } from 'antd';
import React, { useState } from 'react';

const formItemLayout = {
  labelCol: { xs: { span: 24 }, sm: { span: 8 } },
  wrapperCol: { xs: { span: 24 }, sm: { span: 16 } },
};

// 
const initialData = [
  {
    key: '1',
    id: 1,
    userName: 'Li Ming',
    updateTime: '2025-02-15',
  },
  {
    key: '2',
    id: 2,
    userName: 'Zhang Wei',
    updateTime: '2025-02-18',
  },
  {
    key: '3',
    id: 3,
    userName: 'Sarah Connor',
    updateTime: '2025-02-20',
  },
  {
    key: '4',
    id: 4,
    userName: 'Wang Fang',
    updateTime: '2025-02-22',
  },
  {
    key: '5',
    id: 5,
    userName: 'Michael Jackson',
    updateTime: '2025-02-25',
  },
];

const UserTable = () => {
  const [data, setData] = useState(initialData);
  const [editDialogVisible, setEditDialogVisible] = useState(false);
  const [currentUser, setCurrentUser] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize] = useState(5);
  const [form] = Form.useForm();

  const columns = [
    {
      title: '',
      dataIndex: 'id',
      key: 'id',
      width: 80,
    },
    {
      title: '',
      dataIndex: 'userName',
      key: 'userName',
      width: 200,
      render: (text) => <a style={{ color: '#1890ff' }}>{text}</a>,
    },
    {
      title: '',
      dataIndex: 'updateTime',
      key: 'updateTime',
      width: 200,
    },
    {
      title: '',
      key: 'action',
      render: (_, record) => (
        <Space size="small">
          <Button size="small" type="primary" onClick={() => handleEdit(record)}>
            
          </Button>
          <Button size="small" danger onClick={() => handleDelete(record)}>
            
          </Button>
        </Space>
      ),
    },
  ];

  const handleEdit = (record) => {
    setCurrentUser(record);
    form.setFieldsValue({
      name: record.userName,
      password: '',
      confirmpassword: '',
    });
    setEditDialogVisible(true);
  };

  const handleEditSubmit = () => {
    form.validateFields()
      .then((values) => {
        console.log(':', values);
        message.success('!');
        setEditDialogVisible(false);
        form.resetFields();
      })
      .catch((error) => {
        console.log(':', error);
      });
  };

  const handleEditCancel = () => {
    setEditDialogVisible(false);
    form.resetFields();
  };

  const handleDelete = (record) => {
    Modal.confirm({
      title: '',
      content: ', ?',
      okText: '',
      cancelText: '',
      type: 'warning',
      onOk: () => {
        console.log(':', record.id);
        message.success('!');
        setData(data.filter(item => item.id !== record.id));
      },
      onCancel: () => {
        message.info('');
      },
    });
  };

  return (
    <>
      <Table
        columns={columns}
        dataSource={data}
        pagination={{
          current: currentPage,
          pageSize: pageSize,
          total: 100,
          showSizeChanger: false,
          position: ['bottomLeft'],
          onChange: (page) => {
            setCurrentPage(page);
            console.log('', page, '');
          },
        }}
      />

      <Modal
        title=""
        open={editDialogVisible}
        onOk={handleEditSubmit}
        onCancel={handleEditCancel}
        okText=""
        cancelText=""
      >
        <Form
          {...formItemLayout}
          form={form}
          name="editUser"
        >
          <Form.Item
            name="name"
            label=""
            rules={[{
              required: true,
              message: '!',
            }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="password"
            label=""
            rules={[{
              required: true,
              message: '!',
            }]}
            hasFeedback
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            name="confirmpassword"
            label=""
            dependencies={['password']}
            hasFeedback
            rules={[{
              required: true,
              message: '!',
            }, ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error('!'));
              },
            })]}
          >
            <Input.Password />
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
};

export default UserTable;
