import { Input, Button, Modal, message, Form } from 'antd';
import React, { useState, useRef, forwardRef, useImperativeHandle } from 'react';

const formItemLayout = {
  labelCol: { xs: { span: 24 }, sm: { span: 6 } },
  wrapperCol: { xs: { span: 24 }, sm: { span: 18 } },
};

const AddQuestionForm = forwardRef(({ onSubmit }, ref) => {
  const [form] = Form.useForm();

  useImperativeHandle(ref, () => ({
    submit: () => {
      form.submit();
    },
    reset: () => {
      form.resetFields();
    }
  }));

  const onFinish = (values) => {
    console.log('Received values of form: ', values);
    if (onSubmit) {
      onSubmit(values);
    }
  };

  return (
    <Form
      {...formItemLayout}
      form={form}
      name="addQuestion"
      onFinish={onFinish}
      scrollToFirstError
    >
      <Form.Item
        name="question"
        label=""
        rules={[{
          required: true,
          message: '!',
        }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="optiona"
        label="a"
        rules={[{
          required: true,
          message: 'a!',
        }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="optionb"
        label="b"
        rules={[{
          required: true,
          message: 'b!',
        }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="optionc"
        label="c"
        rules={[{
          required: true,
          message: 'c!',
        }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="optiond"
        label="d"
        rules={[{
          required: true,
          message: 'd!',
        }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="answer"
        label=""
        rules={[{
          required: true,
          message: '!',
        }]}
      >
        <Input placeholder="(a/b/c/d)" />
      </Form.Item>
    </Form>
  );
});

const SearchQuestion = () => {
  const [open, setOpen] = useState(false);
  const formRef = useRef(null);

  const showModal = () => {
    setOpen(true);
  };

  const handleOk = () => {
    if (formRef.current) {
      formRef.current.submit();
    }
  };

  const handleCancel = () => {
    if (formRef.current) {
      formRef.current.reset();
    }
    setOpen(false);
  };

  const onSearch = (values) => {
    const keyword = values.keyword;
    if (!keyword || keyword.trim() === '') {
      message.warning('!');
      return;
    }
    console.log(':', keyword);
    message.info('');
  };

  const handleSubmit = (values) => {
    console.log(':', values);
    message.success('!');
    if (formRef.current) {
      formRef.current.reset();
    }
    setOpen(false);
  };

  return (
    <>
      <Form layout="inline" onFinish={onSearch} style={{ marginBottom: '16px' }}>
        <Form.Item label="" name="keyword">
          <Input placeholder="" style={{ width: 200 }} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
        <Form.Item>
          <Button type="primary" onClick={showModal}>
            
          </Button>
        </Form.Item>
      </Form>

      <Modal
        title=""
        open={open}
        onOk={handleOk}
        onCancel={handleCancel}
        okText=""
        cancelText=""
        width={600}
      >
        <AddQuestionForm ref={formRef} onSubmit={handleSubmit} />
      </Modal>
    </>
  );
};

export default SearchQuestion;
